import React, { useState, useEffect } from 'react'
import Grid from '@mui/material/Grid';
import axios from 'axios';
import Typography from '@mui/material/Typography';
import BASE_URL from '../constant/constants'
import ImageModal from './ImageModal';
import Box from '@mui/material/Box';
import CircularProgress from '@mui/material/CircularProgress';

export default function History() {
    const [image, setImage] = useState([])
    const [showloader, setShowloader] = useState(false)
    useEffect(() => {
        setShowloader(true);
        axios.get(BASE_URL + "/api/history")
            .then(res => {
                setTimeout(function () {
                    //your code to be executed after 1 second
                    setImage(res.data.images);
                    setShowloader(false);
                }, 1300);
                // setShowloader(false);
                console.log(res.data);

            })
            .catch(err => {
                setShowloader(false);
            });
    }, [])

    const [open, setOpen] = React.useState(false);
    const [imageurl, setImageurl] = useState("")
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    return (
        <div className='mt-5'>
            {
                !showloader ?
                    <div>
                        {
                            image.length ?
                                <div>
                                    <div>
                                        <ImageModal handleOpen={handleOpen} handleClose={handleClose} open={open} src={imageurl} />
                                    </div>
                                    <Grid container columns={{ xs: 4, sm: 4, md: 8 }}>
                                        {image.map((curr, index) => (
                                            <Grid item xs={4} sm={4} md={4} key={index} className="px-4 py-2" >
                                                <div>
                                                    <Grid container columns={{ xs: 1, sm: 12, lg: 16 }} style={{ backgroundColor: "rgb(247,249,252)" }} className="shadow p-3 mb-5  rounded">
                                                        <Grid item xs={2} sm={6} lg={4}>
                                                            {/* <div > */}
                                                            <Box
                                                                display="flex"
                                                                justifyContent="center"
                                                                alignItems="center"
                                                            // minHeight="100vh"
                                                            >
                                                                <img onClick={(e) => { setImageurl(e.target.src); console.log(e.target); handleOpen() }} src={curr.url} style={{ width: "150px", height: "150px", backgroundColor: "transparent", cursor: "zoom-in" }} alt="" />
                                                            </Box>

                                                            {/* </div> */}
                                                        </Grid>
                                                        <Grid item xs={2} sm={6} lg={12} fullWidth>
                                                            <div className='p-3'>
                                                                {/* <p style={{ fontWeight: "bold" }}>Result:</p> */}
                                                                <Typography variant="h5" gutterBottom>
                                                                    Result:
                                                                </Typography>
                                                                {
                                                                    Object.entries(curr.result).map(([key, value]) => (
                                                                        <div key={key}>{key} : {value}%</div>
                                                                    ))
                                                                }
                                                                <div>

                                                                    <Typography variant="subtitle2" gutterBottom>
                                                                        Date: {new Date(curr.time).toLocaleDateString()}     Time: {new Date(curr.time).toLocaleTimeString()}
                                                                    </Typography>
                                                                    {/* <div className='mt-2'>
                                                                        <Box
                                                                            display="flex"
                                                                            justifyContent="right"
                                                                            alignItems="right"
                                                                        >
                                                                            <Button size="small" variant="outlined" startIcon={<DeleteIcon />} color="error">
                                                                                Delete
                                                                            </Button>
                                                                        </Box>

                                                                    </div> */}
                                                                </div>
                                                            </div>

                                                        </Grid>
                                                    </Grid>

                                                </div>
                                            </Grid>
                                        ))}
                                    </Grid>
                                </div>
                                : <div>
                                    <Typography variant="h5" className="text-center" gutterBottom>
                                        No past uploads found
                                    </Typography>
                                </div>
                        }
                    </div>
                    :
                    <div className="mt-4">
                        <Box display="flex"
                            justifyContent="center"
                            alignItems="center">
                            <CircularProgress />
                        </Box>
                        <Typography variant="h5" className="text-center mt-4" gutterBottom>
                            Hold tight, we're fetching your history
                        </Typography>


                    </div>
            }
        </div>
    )
}
