import React from 'react'
import '../css/contact.css'
export default function Contact() {
  return (
    <div className='mt-5'>
      <form className="cform shadow-lg p-3 mb-5 bg-white rounded">
        <h2 className="ch2">CONTACT US</h2>
        <p className="cp" type="Name:"><input className='cinput' placeholder="Write your name here.." /></p>
        <p className="cp" type="Email:"><input className='cinput' placeholder="Let us know how to contact you back.." /></p>
        <p className="cp" type="Message:"><input className='cinput' placeholder="What would you like to tell us.." /></p>
        <button className="cbutton">Send Message</button>
        <div className="cdiv">
          <span className="fa fa-phone cspan" />+91 9819728310
          <span className="fa fa-envelope-o cspan" /> ahmr@pict.edu
        </div>
      </form>
    </div>

  )
}
