import React from 'react'
import Grid from '@mui/material/Grid';
import Container from '@mui/material/Container';
import img1 from '../images/myimg.png';
import img2 from '../images/img4.png';
import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineOppositeContent from '@mui/lab/TimelineOppositeContent';
import TimelineDot from '@mui/lab/TimelineDot';
import Typography from '@mui/material/Typography';
import LoginIcon from '@mui/icons-material/Login';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import DoneAllIcon from '@mui/icons-material/DoneAll';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
// import Button from '@mui/material/Button';
import { NavLink } from 'react-router-dom'
import ArrowCircleRightOutlinedIcon from '@mui/icons-material/ArrowCircleRightOutlined';
import 'animate.css';

export default function Main() {
    return (
        <div>
            <Container maxWidth="1000px" className="my-5 py-5" >
                <Grid container spacing={{ xs: 2, md: 2 }} columns={{ xs: 1, sm: 8, md: 12 }} justifyContent="center"
                    alignItems="center">
                    <Grid item xs={2} sm={4} md={4}>
                        <div maxWidth="230px">
                            <img src={img1} style={{ maxWidth: "100%", backgroundColor: "transparent" }} alt="" />
                        </div>
                    </Grid>
                    <Grid item xs={2} sm={4} md={4}>
                        <div>
                            <h1 style={{ fontFamily: "Microsoft YaHei UI", fontSize: "50px", fontWeight: "lighter" }}>
                                Say goodbye to traditional methods of wheat grain testing and embrace our cutting-edge technology
                            </h1>
                            <div className='mt-3'>
                                {/* <Button variant="outlined" size="medium" endIcon={<ArrowCircleRightOutlinedIcon />}>
                                    Try It Out
                                </Button> */}
                                <NavLink to="/upload" > <button type="button" class="btn btn-outline-dark btn-lg">Try It Out <ArrowCircleRightOutlinedIcon /></button></NavLink>
                               
                            </div>
                        </div>
                    </Grid>
                </Grid>
            </Container>
            <div style={{ backgroundColor: "black", color: "white" }} className="py-5">
                <p style={{ fontFamily: "Microsoft JhengHei UI", fontSize: "60px", fontWeight: "normal", textAlign: "center" }}>Steps To Follow</p>
                <Timeline position="alternate">
                    <TimelineItem>
                        <TimelineOppositeContent
                            sx={{ m: 'auto 0' }}
                            align="right"
                            variant="body2"
                            color="text.secondary"
                        >
                            {/* 9:30 am */}
                        </TimelineOppositeContent>
                        <TimelineSeparator>
                            <TimelineConnector />
                            <TimelineDot>
                                <LoginIcon />
                            </TimelineDot>
                            <TimelineConnector />
                        </TimelineSeparator>
                        <TimelineContent sx={{ py: '16px', px: 2 }}>
                            <Typography variant="h6" component="span">
                                Sign Up/Sign In
                            </Typography>
                            {/* <Typography>Because you need strength</Typography> */}
                        </TimelineContent>
                    </TimelineItem>
                    <TimelineItem style={{ marginTop: "20px" }}>
                        <TimelineOppositeContent
                            sx={{ m: 'auto 0' }}
                            variant="body2"
                            color="text.secondary"
                        >
                            {/* 10:00 am */}
                        </TimelineOppositeContent>
                        <TimelineSeparator>
                            <TimelineConnector />
                            <TimelineDot color="primary">
                                <CloudUploadIcon />
                            </TimelineDot>
                            <TimelineConnector />
                        </TimelineSeparator>
                        <TimelineContent sx={{ py: '16px', px: 2 }}>
                            <Typography variant="h6" component="span">
                                Upload Image
                            </Typography>
                            {/* <Typography>Because it&apos;s awesome!</Typography> */}
                        </TimelineContent>
                    </TimelineItem>
                    <TimelineItem style={{ marginTop: "20px" }}>
                        <TimelineSeparator>
                            <TimelineConnector />
                            <TimelineDot color="primary" variant="outlined">
                                <AccessTimeIcon />
                            </TimelineDot>
                            <TimelineConnector sx={{ bgcolor: 'secondary.main' }} />
                        </TimelineSeparator>
                        <TimelineContent sx={{ py: '16px', px: 2 }}>
                            <Typography variant="h6" component="span">
                                Wait
                            </Typography>
                            {/* <Typography>Because you need rest</Typography> */}
                        </TimelineContent>
                    </TimelineItem>
                    <TimelineItem style={{ marginTop: "20px" }}>
                        <TimelineSeparator>
                            <TimelineConnector sx={{ bgcolor: 'success.main' }} />
                            <TimelineDot color="success">
                                <DoneAllIcon />
                            </TimelineDot>
                            <TimelineConnector />
                        </TimelineSeparator>
                        <TimelineContent sx={{ py: '16px', px: 2 }}>
                            <Typography variant="h6" component="span">
                                Get Results
                            </Typography>
                            {/* <Typography>Because this is the life you love!</Typography> */}
                        </TimelineContent>
                    </TimelineItem>
                </Timeline>
            </div>
            <Container maxWidth="1000px" className="my-5 py-5" >
                <Grid container spacing={{ xs: 2, md: 2 }} columns={{ xs: 1, sm: 8, md: 12 }} justifyContent="center"
                    alignItems="center">

                    <Grid item xs={2} sm={4} md={4}>
                        <div>
                            <h1 style={{ fontFamily: "Microsoft YaHei UI", fontSize: "50px", fontWeight: "lighter", }}>
                                Save time and improve accuracy with our state-of-the-art wheat quality evaluation
                            </h1>
                        </div>
                    </Grid>
                    <Grid item xs={2} sm={4} md={4} >
                        <div maxWidth="230px">
                            <img src={img2} style={{ maxWidth: "100%", backgroundColor: "transparent" }} alt="" />
                        </div>
                    </Grid>


                </Grid>
            </Container>

        </div>
    )
}
