import React, { useEffect } from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Routes, Navigate  } from "react-router-dom";
import Home from './Home';
import About from './components/About';
import Contact from './components/Contact';
import Main from './components/Main';
import Signin from './components/Signin';
import Signup from './components/Signup';
import { ToastContainer } from 'react-toastify';
import { useAuth } from './states/AuthState'
import axios from 'axios';
import Upload from './components/Upload';
import BASE_URL from './constant/constants'
import History from './components/History';
function App() {
  axios.defaults.withCredentials=true;
  const auth = useAuth()


  useEffect(() => {
    axios.get(BASE_URL+"/api/status")
  .then(res => {
    // console.log(res.data)
    
    auth['first_name'].set(res.data.user?.firstName)
    auth['last_name'].set(res.data.user?.lastName)
    auth['email'].set(res.data.user?.email)
    auth['username'].set(res.data.user?.username)
    auth['isAuthenticated'].set(true)
    // console.log("Testing Status")
    // console.log(auth.username.get())
  })
  .catch(err => {
    // console.log("Hello")
    auth.isLoading.set(false)
  });
})
  return (
    <Router >
      <ToastContainer />
      <Home/>
      <Routes>

        <Route exact path="/about" element={<About/>} />                  
        <Route exact path="/" element={<Main/>} />                  
        <Route exact path="/signin" element={
          auth.isAuthenticated.get()?
          <Navigate replace to={"/"} />:<Signin/>
        } />            

        <Route exact path="/signup" element={
           auth.isAuthenticated.get()?
           <Navigate replace to={"/"} />:<Signup/>
        } />      

         <Route exact path="/upload" element={
           !auth.isAuthenticated.get()?
           <Navigate replace to={"/signin"} />:<Upload/>
        } />         

         <Route exact path="/history" element={
           !auth.isAuthenticated.get()?
           <Navigate replace to={"/signin"} />:<History/>
        } />     

        <Route exact path="/contact" element={<Contact/>} />                  
	    </Routes>
    </Router>
   
  );
}

export default App;
