import { useState } from 'react'
import { NavLink } from 'react-router-dom'
import MenuIcon from '@mui/icons-material/Menu';
import { useAuth } from '../states/AuthState'
import axios from 'axios';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import '../css/navbar.css'
import logo from '../images/AHMR-logos_black.png'
import BASE_URL from '../constant/constants'
import '../js/extnav'
// import Cookies from 'js-cookie'
const Navbar = () => {
  const auth = useAuth()
  // console.log("HelloUser")
  // console.log(auth.isAuthenticated.get())
  const [showNavbar, setShowNavbar] = useState(false)

  const handleShowNavbar = () => {
    setShowNavbar(!showNavbar)
  }
  const logout = () => {
    axios.get(BASE_URL + "/api/logout")
      .then(response => {
        auth['first_name'].set('')
        auth['last_name'].set('')
        auth['email'].set('')
        auth['username'].set('')
        auth['isAuthenticated'].set(false)
        toast.success('Successfully Logged out', {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      })
      .catch(err => {
        toast.error('Server Error While Logging Out', {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      });
  }
  return (
    <nav className="navbar" style={{zIndex:122020}}>
      <div className="container">
        <div className="logo">
          {/* <h2>Hello Mayur</h2> */}
          <img src={logo} alt="" style={{ width: "170px" }} />
        </div>
        <div className="menu-icon" onClick={handleShowNavbar}>
          <MenuIcon />
        </div>
        <div className={`nav-elements  ${showNavbar && 'active'}`}>
          <ul class="navbar-collapse" data-toggle="collapse" data-target=".nav-collapse" onClick={handleShowNavbar}>
            {/* {
              auth.isAuthenticated.get() ? <li><p>Hello {auth.first_name.get()}</p></li> : null
            } */}
            <li>
              <p><NavLink to="/" >Home</NavLink></p>
            </li>

            {
              auth.isAuthenticated.get() ? null : <li><p><NavLink to="/signin" >Sign Up/Sign In</NavLink></p> </li>
            }
            {
              auth.isAuthenticated.get() ? <li><p><NavLink to="/upload" >Upload Image</NavLink></p></li> : null
            }
            {
              auth.isAuthenticated.get() ? <li><p><NavLink to="/history" >History</NavLink></p></li> : null
            }

            <li>
              <p><NavLink to="/about" >About Us</NavLink></p>
            </li>
            <li>
              <p><NavLink to="/contact" >Contact Us</NavLink></p>
            </li>
            <li>

              {
                auth.isAuthenticated.get() ? <p style={{ cursor: 'pointer' }} onClick={logout}>Logout</p> : null
              }
            </li>
          </ul>
        </div>
      </div>
    </nav>
  )
}

export default Navbar