import React, { useState } from 'react'
import Container from '@mui/material/Container';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';
import FileUploadOutlinedIcon from '@mui/icons-material/FileUploadOutlined';
import { Buffer } from 'buffer';
import ReactS3Client from 'react-aws-s3-typescript';
import Box from '@mui/material/Box';
import LinearProgress from '@mui/material/LinearProgress';
import CheckCircleOutlinedIcon from '@mui/icons-material/CheckCircleOutlined';
import CircularProgress from '@mui/material/CircularProgress';
import axios from 'axios';
import LoadingButton from '@mui/lab/LoadingButton';
import BASE_URL from '../constant/constants'

Buffer.from('anything', 'base64');
window.Buffer = window.Buffer || require("buffer").Buffer;

const S3_BUCKET = 'beproject2023';





let config = {
    bucketName: S3_BUCKET,
    region: 'us-east-1',
    accessKeyId: process.env.REACT_APP_ACCESS_KEY,
    secretAccessKey: process.env.REACT_APP_SECRET_ACCESS_KEY,
    s3Url: 'https://beproject2023.s3.amazonaws.com'
}



export default function Upload() {

    const [selectedFile, setSelectedFile] = useState(null);
    const [isuploaded, setIsuploaded] = useState(true);
    const [iscompleted, setIscompleted] = useState(false);
    const [gotresult, setGotresult] = useState(false);
    const [result, setResult] = useState()
    const handleFileInput = (e) => {
        setSelectedFile(e.target.files[0]);
    }

    const uploadFile = (file) => {


        const formData1 = new FormData();
        formData1.append('file', file);
        const config2 = {
            headers: {
                'content-type': 'multipart/form-data'
            }

        };

        // return;

        const s3 = new ReactS3Client(config);
        setIsuploaded(false);
        axios.get(BASE_URL + "/api/getawskeys").then((res) => {
            // console.log(res.data)
            config.accessKeyId = res.data.ACCESS_KEY;
            config.secretAccessKey = res.data.SECRET_ACCESS_KEY;
            // console.log(config);
            s3.uploadFile(file)
                .then((data) => {
                    setIsuploaded(true); setIscompleted(true); setGotresult(true);
                    // Uploading image to flask server
                    axios.post("https://beproject-flask.onrender.com/success", formData1, config2)
                        .then((res) => {
                            console.log("Got response from flask server");
                            console.log(res);
                            setResult(res.data);
                            console.log("This was the response");
                            setGotresult(false)
                            var senddata = { url: data.location,result:res.data }
                            console.log("Sending URL and Result to Server")
                            // console.log(senddata);
                            axios(
                                {
                                    url: BASE_URL + "/api/uploadimage",
                                    method: "POST",
                                    data: senddata,

                                })
                                .then(res => {
                                    console.log(res);
                                })
                                .catch(err => {
                                    console.log(err)
                                    console.log("failed to upload to server")
                                })
                        }).catch((err) => {
                            console.log(err)
                            console.log("Error while uploading to flask")
                        });
                  



                })
                .catch(err => { console.error(err); console.log("Failed to Upload to S3") })
        });



        const formData = new FormData();
        formData.append('myImage', file);
        // const config1 = {
        //     headers: {
        //         'content-type': 'multipart/form-data'
        //     }

        // };
        // axios.post(BASE_URL + "/api/processimage", formData, config1)
        //     .then((response) => {
        //         setIsuploaded(true); setIscompleted(true); setGotresult(true);
        //         console.log("File sent for processing");
        //     }).catch((error) => {
        //     });




    }
    function refreshPage() {
        setSelectedFile(null);
        setGotresult(false);
        setIscompleted(false);
        setIsuploaded(true);
    }


    return (
        <div>
            <div className="mt-5">

                <Container maxWidth="sm" className="shadow-lg p-3 mb-5 bg-white rounded">
                    <Card sx={{ maxWidth: "100%" }} variant="outlined">
                        <CardMedia
                            sx={{ height: 280 }}
                            image="../images/img1.jpg"
                            title="green iguana"
                        />
                        <CardContent>

                            <Typography gutterBottom variant="h3" component="div" align="center">
                                Upload Image
                            </Typography>
                            <Typography variant="body2" color="text.secondary">
                                <Stack sx={{ width: '100%' }} spacing={2} className="mb-3">
                                    <Alert severity="info">Please upload image only in jpg or png format</Alert>
                                </Stack>
                            </Typography>
                            {
                                !iscompleted ? <div>
                                    <input type="file" id="images" accept="image/*" onChange={handleFileInput} required />
                                    <div className="mt-4">
                                        {
                                            isuploaded ? selectedFile != null ? <Button fullWidth={true} onClick={() => uploadFile(selectedFile)} variant="outlined" >Upload <FileUploadOutlinedIcon /></Button> : <Button fullWidth={true} onClick={() => uploadFile(selectedFile)} variant="outlined" disabled>Upload <FileUploadOutlinedIcon /></Button> :
                                                <LoadingButton fullWidth={true} onClick={() => uploadFile(selectedFile)} loading loadingIndicator="Loading…" variant="outlined">Upload <FileUploadOutlinedIcon /></LoadingButton >
                                        }
                                    </div>

                                    <div>
                                        {
                                            isuploaded ? null : <Box sx={{ width: '100%' }} className="mt-3">
                                                <LinearProgress />
                                            </Box>
                                        }
                                    </div>
                                </div> : <div>
                                    <div>
                                        <Button variant="outlined" onClick={refreshPage}>Upload New Image</Button>
                                        {/* <button onClick={refreshPage}>Upload New Image</button> */}
                                    </div>
                                    <Typography variant="h5" className="text-success mt-4">
                                        Uploaded Successfully <CheckCircleOutlinedIcon color="success" />
                                    </Typography>
                                    <div>
                                        {
                                            gotresult ? <div>
                                                <Typography variant="h5" className="text-primary mt-4">
                                                    Getting Results
                                                </Typography>
                                                <Box sx={{ display: 'flex' }} className="mt-3">
                                                    <CircularProgress />
                                                </Box></div> :
                                                <div className="mt-3">
                                                    <div className="text-success">Result: </div>
                                                    {
                                                        Object.entries(result).map(([key, value]) => (
                                                            <div key={key}>{key} : {value}%</div>
                                                        ))
                                                    }
                                                </div>
                                        }
                                    </div>
                                </div>
                            }
                        </CardContent>

                    </Card>
                </Container>
            </div>
        </div>
    )
}
